 100;\n}\n\na {\n  text-decoration: none;\n}\n\nh1, h2, h3, h4, h5, h6{\n  font-family: 'Merriweather', sans-serif;\n}\n\n.highlight-text{\n  color: $accent-color;\n}\n\nimg {\n  display: block;\n}\n\na.btn{\n  display: inline-block;\n}\n\n.btn{\n  padding: .8rem 2rem;\n  border-radius: 4px;\n  font-size: 13pt;\n}\n\n.btn-outline-white{\n  background: transparent;\n  color: #fff;\n  border: 2px solid #fff;\n  transition: 0.3s all;\n}\n\n.btn-outline-white:hover{\n  background: #fff;\n  color: $gray-100;\n}\n\n.container {\n  width: 100%;\n  margin: 0 auto;\n}\n\n.content-header{\n  margin-bottom: 30px;\n\n  h2{      \n    color: $accent-color;\n    font-size: 2.25em;\n  }\n\n  p{\n    font-size: 1.25em;\n    color: $gray-100;\n  }\n}\n\n@media screen and (min-width: 1200px) {\n  .container {\n    max-width: 1200px;\n  }\n}\n\n \n@media screen and (max-width: 576px) {\n  .content-header{\n    text-align: center;\n    padding: 40px 20px 0px 20px;\n    p{\n      padding: 0 1rem;\n    }\n  }\n}\n\n#hamburger{\n  background: #ebebeb;\n  color: $gray-100;\n  display: none;\n  text-decoration: none;\n  font-size: 1.5em;\n  padding: 10px 20px;\n  border-radius: 4px;\n}\n\nheader{\n  background: #fff;\n  box-shadow: 0 3px 14px rgb(0 0 0 / 14%);\n  top: 0;\n  width: 100%;\n  position: sticky;\n  padding: 1rem 2rem;\n  z-index: 2;\n  \n  .container{\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n\n    .header-content{\n      padding: .8rem 1.2rem;\n      display: flex;\n      align-items: center;\n      flex-direction: row;\n      justify-content: space-between;\n    }\n\n    .logo{\n      font-family: 'Merriweather', sans-serif;\n      color: $accent-color;\n      font-size: 2em;\n    }\n  }\n  \n  .highlight-text{\n    font-weight: 700;\n  }\n}\n\nnav {\n  ul {\n    list-style: none;\n    display: flex;\n\n    \n    li a{\n      text-transform: uppercase;\n      color: $gray-100;\n      padding: 1rem;\n      transition: 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);\n    }\n\n    li a:hover{\n      color: $accent-color;\n    }\n\n    li a:focus{\n      color: $accent-color;\n    }\n  }\n\n  .menu-active{\n    color: $accent-color;\n    font-weight: 700;\n  }\n\n\n}\n\n@media screen and (max-width: 576px){\n  header{\n    padding: 0px;\n    text-align: center;\n  }\n  #hamburger{\n    display: block;\n  }\n  #navbar{\n    display: none;\n  }\n  #navbar.open{\n    display: block;\n  }\n  \n  header{\n    .logo{\n      padding: 1rem 0;\n    }\n    .container{\n      flex-direction: column;\n      \n      .header-content{\n        width: 100%;\n      }\n    }\n  }\n  nav{\n\n\n    ul{\n      text-align: center;\n      flex-direction: column;\n\n      li{\n        padding: 1rem;\n      }\n    }\n  }\n} \n\n.hero-el {\n  background: url('../public/images/heros/hero-image_4.jpg') no-repeat fixed;\n  background-size: cover;\n  width: 100%;\n  height: 550px;\n  text-align: center;\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n\n  .highlight-text{\n    color: $yellow-accent;\n  }\n\n  .filter{\n    background: rgba(30, 31, 35, 0.89);\n    position: absolute;\n    top: 0;\n    width: 100%;\n    height: 100%;\n  }\n\n  .hero-content{\n    position: relative;\n    color: #fff;\n    z-index: 1;\n\n    .btn{\n      margin-top: 15px;\n    }\n\n    h1{\n      font-size: 5em;\n    }\n\n    p{\n      font-size: 1.5em;\n      max-width: 500px;\n      margin: 0 auto;\n    }\n  }\n}\n\nmain{\n  padding: 2rem;\n  box-sizing: border-box;\n}\n\n.list-resto{\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  gap: 40px;\n}\n\n@media screen and (max-width: 1024px){\n  .list-resto{\n    grid-template-columns: 1fr 1fr;\n  }\n}\n\n@media screen and (max-width: 768px){\n  .list-resto{\n    grid-template-columns: 1fr 1fr;\n  }\n  .card-body{\n    padding: 0;\n    box-sizing: border-box;\n  }\n}\n\n@media screen and (max-width: 576px){\n  main{\n    padding: 0;\n  }\n  .hero-el .hero-content h1{\n    font-size: 3em;\n  }\n  .hero-el .hero-content p{\n    font-size: 1.1em;\n    padding: 1rem 2.5rem;\n  }\n  .list-resto{\n    grid-template-columns: 1fr;\n    gap: 0px;\n  }\n}\n\n.card-resto{\n  background: #fff;\n  border-radius: 4px;\n  overflow: hidden;\n  \n  .card-img{ \n    border-radius: 4px;\n    height: 200px;\n    overflow: hidden;\n    position: relative;\n\n    .rating-label{\n      border-radius: 4px;\n      background: rgba(0, 0, 0, 0.52);\n      backdrop-filter: blur(4px);\n      position: absolute;\n      top: 15px;\n      left: 15px;\n      color: #fff;\n      padding: .5rem 1rem;\n      display: flex;\n      align-items: center;\n    }\n\n    .rating-label::before{\n      background-image: url('../public/icons/star-solid.svg');\n      background-size: 20px 20px;\n      content: '';\n      display: inline-block;\n      margin: 0 10px 0 0;\n      width: 20px;\n      height: 20px;\n    }\n\n    img{\n      width: 100%;\n      height: auto;\n      transition: 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);\n    }\n  }\n  \n  .card-body{\n \n    \n    h3{\n      font-size: 1.55em;\n      margin: 15px 0 8px 0;\n      color: $gray-100;\n    }\n\n    p{\n      color: #888;\n      width: 340px;\n      overflow: hidden;\n      white-space: nowrap;\n      text-overflow: ellipsis;\n    }\n\n    .loc-label{\n      display: flex;\n      margin: 10px 0px;\n    }\n\n    .loc-label::before{\n      background-image: url('../public/icons/location-dot-solid.svg');\n      background-size: 20px 20px;\n      content: '';\n      display: inline-block;\n      margin: 0 5px 0 0;\n      width: 20px;\n      height: 20px;\n    }\n  }\n}\n\nfooter{\n  background: #F2E1D8;\n  text-align: center;\n  padding: 3% 0;\n\n  h2{\n    color: $accent-color;\n    font-size: 4.5em;\n  }\n\n  p{\n    color: $gray-100;\n    font-size: 1.5em;\n    margin-bottom: 10px;\n  }\n\n  #copy{\n    color: $gray-200;\n    font-size: 11pt;\n  }\n\n  #love::before{\n    background-